﻿import React, { useMemo, useState } from 'react';

const LoginPage = ({ copy, variant = 'default' }) => {
    const labelClass = 'text-sm font-semibold text-slate-200 md:w-40';
    const inputClass =
        'required w-full rounded-xl border border-white/15 bg-slate-900/60 px-4 py-3 text-sm text-slate-100 placeholder-slate-400 transition focus:border-sky-400 focus:outline-none focus:ring-2 focus:ring-sky-500/40';

    const isStreamerMode = variant === 'streamer';
    const streamerCopy = copy.streamer || {};
    const youtubeCopy = streamerCopy.youtube || {};
    const hasYoutubeConfig = isStreamerMode && Object.keys(youtubeCopy).length > 0;

    const loginTitle = isStreamerMode && streamerCopy.loginTitle ? streamerCopy.loginTitle : copy.login.title;
    const loginSubtitle = isStreamerMode ? streamerCopy.loginSubtitle : null;

    const [youtubeStatus, setYoutubeStatus] = useState('idle');
    const [youtubeMessage, setYoutubeMessage] = useState('');

    const youtubeStatusLabel = useMemo(() => {
        if (!hasYoutubeConfig) {
            return '';
        }
        return youtubeCopy.status?.[youtubeStatus] || youtubeCopy.status?.idle || '';
    }, [hasYoutubeConfig, youtubeCopy.status, youtubeStatus]);

    const handleYoutubeConnect = (event) => {
        event.preventDefault();
        if (!hasYoutubeConfig) {
            return;
        }
        if (youtubeStatus === 'linked') {
            setYoutubeStatus('idle');
            setYoutubeMessage(youtubeCopy.disconnected || 'YouTube channel disconnected.');
            return;
        }
        setYoutubeStatus('linked');
        setYoutubeMessage(youtubeCopy.connected || 'YouTube channel linked. Streaming will auto-start.');
    };

    const youtubeBadgeClasses = youtubeStatus === 'linked'
        ? 'inline-flex items-center rounded-full border border-emerald-400/60 bg-emerald-500/20 px-3 py-1 text-xs font-semibold text-emerald-200'
        : 'inline-flex items-center rounded-full border border-white/20 bg-white/10 px-3 py-1 text-xs font-semibold text-slate-200';

    const youtubeButtonLabel = youtubeStatus === 'linked'
        ? youtubeCopy.unlinkCta || 'Disconnect'
        : youtubeCopy.linkCta || 'Link YouTube';

    return (
        <div className="row row-1 w-full !max-w-none !bg-transparent !p-0 !shadow-none">
            <div className="grid w-full gap-6 xl:grid-cols-[1.05fr,_0.95fr]">
                <form
                    name="login-form"
                    id="login-form"
                    className="login-form space-y-5 rounded-2xl border border-white/10 bg-slate-900/60 p-6 shadow-xl backdrop-blur"
                    action="#"
                    method="post"
                >
                    <div className="flex flex-col gap-2">
                        <h3 className="form-title text-left text-2xl font-semibold text-white">{loginTitle}</h3>
                        {loginSubtitle && <p className="text-sm text-slate-300">{loginSubtitle}</p>}
                    </div>
                    <div className="input-box login-username flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
                        <label htmlFor="username" className={labelClass}>
                            {copy.login.usernameLabel}
                        </label>
                        <input
                            type="text"
                            name="username"
                            id="username"
                            className={inputClass}
                            required
                            autoComplete="on"
                        />
                    </div>
                    <div className="input-box login-password flex flex-col gap-3 md:flex-row md:items-center md:justify-between">
                        <label htmlFor="password" className={labelClass}>
                            {copy.login.passwordLabel}
                        </label>
                        <input type="password" name="password" id="password" className={inputClass} required />
                    </div>
                    <div className="input-box submit-container login-submit flex items-center justify-end gap-3">
                        <div className="loading-container hidden">
                            <img src="./assets/web/loading.gif" alt={copy.login.loadingAlt} />
                        </div>
                        <input
                            type="submit"
                            value={copy.login.submit}
                            className="rounded-full bg-sky-500 px-6 py-2 font-semibold text-white shadow-lg shadow-sky-500/30 transition hover:bg-sky-400"
                        />
                    </div>
                    <div className="input-box response-error text-sm text-rose-400"></div>
                </form>
                <div className="flex flex-col gap-6">
                    {hasYoutubeConfig && (
                        <section className="space-y-5 rounded-2xl border border-white/10 bg-slate-900/60 p-6 shadow-xl backdrop-blur">
                            <div className="flex flex-col gap-2">
                                <div className="flex items-center justify-between gap-3">
                                    <h3 className="text-xl font-semibold text-white">{youtubeCopy.heading}</h3>
                                    {youtubeStatusLabel && <span className={youtubeBadgeClasses}>{youtubeStatusLabel}</span>}
                                </div>
                                {youtubeCopy.description && (
                                    <p className="text-sm text-slate-300">{youtubeCopy.description}</p>
                                )}
                            </div>
                            <form className="space-y-4" onSubmit={handleYoutubeConnect}>
                                <div className="flex flex-col gap-2 text-sm text-slate-200">
                                    <label htmlFor="youtube-channel" className="font-semibold">
                                        {youtubeCopy.fields?.channelLabel}
                                    </label>
                                    <input
                                        type="url"
                                        id="youtube-channel"
                                        name="youtube-channel"
                                        className={inputClass}
                                        placeholder="https://youtube.com/@your-channel"
                                        required
                                    />
                                </div>
                                <div className="flex flex-col gap-2 text-sm text-slate-200">
                                    <label htmlFor="youtube-stream-key" className="font-semibold">
                                        {youtubeCopy.fields?.streamKeyLabel}
                                    </label>
                                    <input
                                        type="text"
                                        id="youtube-stream-key"
                                        name="youtube-stream-key"
                                        className={inputClass}
                                        placeholder="STREAM-XXXX-YYYY-ZZZZ"
                                        required
                                    />
                                </div>
                                <div className="flex items-center gap-2 text-sm text-slate-200">
                                    <input
                                        type="checkbox"
                                        id="youtube-auto-start"
                                        name="youtube-auto-start"
                                        className="h-4 w-4 rounded border-white/30 bg-slate-900/70 text-sky-500 focus:ring-sky-500/50"
                                        defaultChecked
                                    />
                                    <label htmlFor="youtube-auto-start" className="cursor-pointer select-none">
                                        {youtubeCopy.fields?.autoStartLabel}
                                    </label>
                                </div>
                                <div className="flex items-center justify-between gap-3">
                                    <button
                                        type="submit"
                                        className="inline-flex items-center justify-center rounded-full bg-rose-500 px-5 py-2 text-sm font-semibold text-white shadow-lg shadow-rose-500/30 transition hover:bg-rose-400"
                                    >
                                        {youtubeButtonLabel}
                                    </button>
                                    {youtubeCopy.learnMore && (
                                        <a
                                            className="text-xs font-semibold uppercase tracking-wide text-slate-300 hover:text-white hover:underline"
                                            href={youtubeCopy.learnMore}
                                            target="_blank"
                                            rel="noreferrer"
                                        >
                                            {youtubeCopy.learnMoreLabel || 'Docs'}
                                        </a>
                                    )}
                                </div>
                            </form>
                            {Array.isArray(youtubeCopy.steps) && youtubeCopy.steps.length > 0 && (
                                <ul className="space-y-2 text-xs text-slate-400">
                                    {youtubeCopy.steps.map((step) => (
                                        <li key={step} className="flex items-start gap-2">
                                            <span className="mt-1 inline-block h-2 w-2 rounded-full bg-rose-400"></span>
                                            <span>{step}</span>
                                        </li>
                                    ))}
                                </ul>
                            )}
                            {youtubeMessage && (
                                <div className="rounded-2xl border border-sky-400/40 bg-sky-500/10 p-3 text-xs text-sky-100">
                                    {youtubeMessage}
                                </div>
                            )}
                            {youtubeCopy.helper && (
                                <p className="text-xs text-slate-400">{youtubeCopy.helper}</p>
                            )}
                        </section>
                    )}
                    <div className="row row-2 firebase-row-container hidden !max-w-none !bg-slate-900/60 !p-6 !shadow-xl space-y-4 rounded-2xl border border-white/10 backdrop-blur">
                        <form name="firebase-login" id="firebase-login" className="firebase-login space-y-4" action="#" method="post">
                            <h3 className="form-title text-xl font-semibold text-white">{copy.firebase.title}</h3>
                            <div className="input-box firebase-username flex flex-col gap-2 text-sm text-slate-200">
                                <label htmlFor="firebase-username" className="font-semibold">
                                    {copy.firebase.usernameLabel}
                                </label>
                                <input
                                    type="text"
                                    name="firebase-username"
                                    id="firebase-username"
                                    className={inputClass}
                                    required
                                />
                            </div>
                            <div className="input-box response-error text-sm text-rose-400"></div>
                        </form>
                        <div className="firebase-container flex flex-1 items-center justify-center rounded-xl border border-dashed border-white/20 bg-slate-900/30 p-4">
                            <div id="firebaseui-auth-container" className="w-full"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default LoginPage;
